:root {
  --hole-distance: 25px;
}

#hole1 {
  top: var(--hole-distance);
  left: var(--hole-distance);
}
#hole2 {
  top: var(--hole-distance);
  right: var(--hole-distance);
}
#hole3 {
  bottom: var(--hole-distance);
  left: var(--hole-distance);
}
#hole4 {
  bottom: var(--hole-distance);
  right: var(--hole-distance);
}

.hole {
  width: 20px;
  height: 20px;
  position: absolute;
  border-radius: 50%;
  background-image: radial-gradient(circle at 99%, #f4f4f4 10%, grey 70%);
  transform: rotate(45deg);
}

#sign-wrapper {
  background-color: #f4f4f4;
  position: relative;
  width: 80%;
  min-width: 340px;
  max-width: 800px;
  height: 90%;
  margin: 2% auto;
  padding: 50px;
  border: 1px solid #e9ecf0;
  border-radius: 45px;
  box-shadow: $box-shadow;
  font-family: "Montserrat", sans-serif;
  font-size: 1rem;
}

#header {
  background-color: #ef5350;
  padding: 20px;
  border-radius: 30px 30px 0 0;
  text-align: center;
}

.h1 {
  text-transform: uppercase;
  color: #f4f4f4;
  font-size: 4.5em;
  line-height: 0.9em;
  letter-spacing: 3px;
  margin: 0;
  font-weight: 900;
}

.strike {
  position: absolute;
  width: 25%;
  height: 10px;
  background-color: #fff;
}
#strike1 {
  top: 115px;
  left: 80px;
}
#strike2 {
  top: 115px;
  right: 80px;
}

#sign-body {
  display: flex;
  flex-wrap: nowrap;
}
#copy-container {
  flex-basis: 60%;
}
#circle-container {
  flex-basis: 40%;
}

.h2,
.p {
  text-align: center;
  color: #1d1e22;
}

.h2 {
  font-size: 3em;
  text-transform: uppercase;
  margin: 40px 0;
  line-height: 0.9em;
}
.p {
  font-size: 20px;
}

@media screen and (max-width: 930px) {
  #sign-wrapper {
    font-size: 0.75rem;
  }
}
@media screen and (max-width: 750px) {
  #sign-wrapper {
    font-size: 0.6rem;
  }
  .h2 {
    margin: 25px 0;
  }
  .strike {
    visibility: hidden;
  }
}
@media screen and (max-width: 600px) {
  #sign-wrapper {
    font-size: 0.4rem;
    padding: 15px;
    border-radius: 25px;
  }
  #header {
    border-radius: 20px 20px 0 0;
  }
  #circle-container {
    width: 50%;
  }
  .hole {
    width: 10px;
    height: 10px;
  }
  :root {
    --hole-distance: 8px;
  }
}
@media screen and (max-width: 450px) {
  #sign-wrapper {
    font-size: 0.34rem;
  }
  .h2 {
    margin: 10px;
  }
  .p {
    font-size: 14px;
  }
}
